import React, { useState, useEffect } from 'react';
import api from '../services/api';
import './SupplierApprovals.css';

interface Supplier {
  _id: string;
  legal_name: string;
  identification_number: string;
  identification_type: string;
  legal_representative_name: string;
  approval_status: 'pending' | 'approved' | 'rejected';
  created_by: { name: string; email: string };
  createdAt: string;
  rejection_reason?: string;
  email?: string;
  phone?: string;
}

const SupplierApprovals: React.FC = () => {
  const [suppliers, setSuppliers] = useState<Supplier[]>([]);
  const [filter, setFilter] = useState<'pending' | 'all'>('pending');
  const [loading, setLoading] = useState(true);
  const [rejecting, setRejecting] = useState<string | null>(null);
  const [rejectionReason, setRejectionReason] = useState('');

  useEffect(() => {
    fetchSuppliers();
  }, [filter]);

  const fetchSuppliers = async () => {
    try {
      const endpoint = filter === 'pending'
        ? '/supplier-approvals/pending'
        : '/supplier-approvals/all';
      const response = await api.get(endpoint);
      setSuppliers(response.data);
    } catch (error) {
      console.error('Error fetching suppliers:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleApprove = async (id: string) => {
    if (!window.confirm('Aprobar este tercero?')) return;

    try {
      await api.post(`/supplier-approvals/${id}/approve`);
      alert('Tercero aprobado exitosamente');
      fetchSuppliers();
    } catch (error: any) {
      alert(error.response?.data?.error || 'Error al aprobar');
    }
  };

  const handleReject = async (id: string) => {
    if (!rejectionReason.trim()) {
      alert('Debes proporcionar una razn del rechazo');
      return;
    }

    try {
      await api.post(`/supplier-approvals/${id}/reject`, { rejection_reason: rejectionReason });
      alert('Tercero rechazado');
      setRejecting(null);
      setRejectionReason('');
      fetchSuppliers();
    } catch (error: any) {
      alert(error.response?.data?.error || 'Error al rechazar');
    }
  };

  const getStatusBadge = (status: string) => {
    const styles = {
      pending: { bg: '#fef3c7', color: '#92400e', text: 'Pendiente' },
      approved: { bg: '#d1fae5', color: '#065f46', text: 'Aprobado' },
      rejected: { bg: '#fee2e2', color: '#991b1b', text: 'Rechazado' }
    };
    const style = styles[status as keyof typeof styles];
    return (
      <span className="status-badge" style={{ background: style.bg, color: style.color }}>
        {style.text}
      </span>
    );
  };

  if (loading) {
    return <div className="loading">Cargando terceros...</div>;
  }

  return (
    <div className="supplier-approvals">
      <div className="page-header">
        <div>
          <h1>Aprobacin de Terceros</h1>
          <p className="subtitle">Revisa y aprueba terceros creados por usuarios</p>
        </div>
        <div className="filter-buttons">
          <button
            onClick={() => setFilter('pending')}
            className={filter === 'pending' ? 'btn-primary' : 'btn-secondary'}
          >
            Pendientes
          </button>
          <button
            onClick={() => setFilter('all')}
            className={filter === 'all' ? 'btn-primary' : 'btn-secondary'}
          >
            Todos
          </button>
        </div>
      </div>

      {suppliers.length === 0 ? (
        <div className="empty-state">
          <h3>No hay terceros {filter === 'pending' ? 'pendientes' : ''}</h3>
          <p>{filter === 'pending' ? 'Todos los terceros han sido revisados' : 'No hay terceros registrados'}</p>
        </div>
      ) : (
        <div className="suppliers-list">
          {suppliers.map((supplier) => (
            <div key={supplier._id} className="supplier-card" data-status={supplier.approval_status}>
              <div className="supplier-header">
                <div className="supplier-info">
                  <h3>{supplier.legal_name}</h3>
                  <div className="supplier-meta">
                    <span><strong>{supplier.identification_type}:</strong> {supplier.identification_number}</span>
                    <span></span>
                    <span><strong>Representante:</strong> {supplier.legal_representative_name}</span>
                  </div>
                  {supplier.email && <p className="supplier-contact"> {supplier.email}</p>}
                  {supplier.phone && <p className="supplier-contact"> {supplier.phone}</p>}
                  <p className="supplier-created">
                    Creado por <strong>{supplier.created_by.name}</strong> el {new Date(supplier.createdAt).toLocaleDateString()}
                  </p>
                  {supplier.rejection_reason && (
                    <div className="rejection-notice">
                      <strong>Razn del rechazo:</strong> {supplier.rejection_reason}
                    </div>
                  )}
                </div>
                <div className="supplier-status">
                  {getStatusBadge(supplier.approval_status)}
                </div>
              </div>

              {supplier.approval_status === 'pending' && (
                <div className="supplier-actions">
                  <button
                    className="btn-approve"
                    onClick={() => handleApprove(supplier._id)}
                  >
                     Aprobar
                  </button>
                  <button
                    className="btn-reject"
                    onClick={() => setRejecting(supplier._id)}
                  >
                     Rechazar
                  </button>
                </div>
              )}

              {rejecting === supplier._id && (
                <div className="rejection-form">
                  <label>Razn del rechazo:</label>
                  <textarea
                    value={rejectionReason}
                    onChange={(e) => setRejectionReason(e.target.value)}
                    rows={3}
                    placeholder="Explica por qu se rechaza este tercero..."
                  />
                  <div className="rejection-actions">
                    <button className="btn-danger" onClick={() => handleReject(supplier._id)}>
                      Confirmar Rechazo
                    </button>
                    <button
                      className="btn-secondary"
                      onClick={() => {
                        setRejecting(null);
                        setRejectionReason('');
                      }}
                    >
                      Cancelar
                    </button>
                  </div>
                </div>
              )}
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default SupplierApprovals;
