import React, { useState, useEffect } from 'react';
import api from '../services/api';
import './Suppliers.css';

interface ThirdPartyType {
  _id: string;
  code: string;
  label: string;
  icon: string;
  description: string;
  fields: any[];
  default_identification_types?: string[];
}

interface Supplier {
  _id: string;
  identification_type: string;
  identification_number: string;
  id_issue_city?: string; // Ciudad de expedicin del documento
  legal_name: string;
  legal_name_short: string;
  legal_representative_name: string;
  legal_representative_id_type: string;
  legal_representative_id_number: string;
  licensee_name?: string; // Nombre del Licenciatario (para PH)
  email?: string;
  phone?: string;
  address?: string;
  city?: string;
  country?: string;
  active: boolean;
  approval_status: 'pending' | 'approved' | 'rejected';
  approved_by?: {
    name: string;
    email: string;
  };
  approved_at?: string;
  rejection_reason?: string;
  third_party_type?: ThirdPartyType;
  custom_fields?: { [key: string]: any };
  createdAt: string;
  created_by?: {
    name: string;
    email: string;
  };
  canDelete?: boolean; // Indica si se puede eliminar (basado en referencias)
  hasReferences?: boolean; // Indica si tiene referencias en contratos/plantillas
  referenceDetails?: {
    contracts: number;
    templates: number;
  };
}

interface SupplierFormData {
  identification_type: string;
  identification_number: string;
  id_issue_city: string; // Ciudad de expedicin del documento
  legal_name: string;
  legal_name_short: string;
  full_name: string; // Para personas naturales
  legal_representative_name: string;
  legal_representative_id_type: string;
  legal_representative_id_number: string;
  licensee_name: string; // Nombre del Licenciatario (para PH)
  email: string;
  phone: string;
  address: string;
  city: string;
  country: string;
  third_party_type: string;
  custom_fields: { [key: string]: any };
}

// Datos de pases y ciudades
